*{
    margin: 0;
    padding: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;   
}


.sambacss {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}

.h1 {
    text-align: center;
}

.text {
    height: 410px;
    text-align: justify;
    border: 1px solid whitesmoke;
    display: block;
    border-radius: 10px;
    font-size: 14px;
    width: 630px;
    padding: 10px;
    margin-left: 600px;
    background: rgb(0, 0, 0);
    opacity: 82%;
    margin-top: 200px;
    position: fixed;
    font-size: 22px;
}

.text-um {
    font-weight: 900 bold;
    color: rgb(0, 255, 34);
}

.text-dois {
    margin-top: 20px;
}

.text-tres {
    color: rgb(0, 255, 64);
    font-weight: 900 bold;
    margin-top: 20px;
}

.text h3 {
    color: white;
}